.G-table {
    &_cols {
        display: flex;
        border-bottom: 2px solid #E9EBEE;
        width: 100%;
        padding-bottom: 10px;
    }

    &_col {
        width: 150px;
        overflow: hidden;
        padding: 5px 7px;
        color: rgb(51, 90, 39);
    }

    &_rows {
        display: flex;
        width: 100%;
        border-bottom: 1px solid #E9EBEE;
        padding: 10px 0;
    }

    &_row {
        width: 150px;
        overflow: hidden;
        padding: 5px 7px;
        
        &_icons {
            display: flex;
            align-items: center;
            justify-content: flex-end;

            > div {
                margin: 0 5px;
                cursor: pointer;
                width: 20px;
            }
        }

        &_view-modal {
            margin: 7px 0;
            font-size: 16px;
            color: rgb(51, 90, 39);
            font-weight: 700;
            font-family: 'Ubuntu', sans-serif;
    
        }
        &_active_state {
            color: green;
        }
        &_inactive_state {
            color: red;
        }
    }

}